<template>
    <p-dialog
        v-model:visible="addBloodDialogvisible"
        modal
        header="Add Blood Test"
        :style="{ width: '800px' }"
    >
        <div style="display: flex">
            <div
                style="flex-basis: 45%; display: flex; flex-direction: column;"
            >
                <!-- <div class="p-field p-grid p-mb-2">
                    <label class="p-col-fixed p-mb-2 p-md-2 p-mb-md-0">
                        Cycle No
                    </label>
                    <div
                        class="p-col-fixed"
                        style="display: flex; align-items: center;"
                    >
                        <p-checkbox v-model="isCycleChecked" :binary="true" />
                        <label class="p-ml-2" style="margin-left: 10px;">
                            {{ cycle ? cycle.cycleId : "--------" }}
                        </label>
                    </div>
                </div> -->
                <div class="p-field p-grid p-mb-2">
                    <label class="p-col-fixed p-mb-2 p-md-2 p-mb-md-0">
                        Patient
                    </label>
                    <div
                        class="p-col-fixed"
                        style="display: flex; align-items: center;"
                    >
                        <label>{{
                            patient.chart ? patient.chart : "--------"
                        }}</label>
                    </div>
                </div>
                <div class="p-field p-grid p-mb-2">
                    <label class="p-col-fixed p-mb-2 p-md-2 p-mb-md-0">
                        Date
                    </label>
                    <div class="p-col-fixed">
                        <p-calendar
                            id="date"
                            v-model="form.bloodTestResultDate"
                            dateFormat="yy-mm-dd"
                            :showIcon="true"
                            class="custom-calendar"
                        />
                    </div>
                </div>
                <div class="p-field p-grid p-mb-2">
                    <label class="p-col-fixed p-mb-2 p-md-2 p-mb-md-0">
                        Doctor
                    </label>
                    <div class="p-col-fixed">
                        <p-dropdown
                            v-model="form.physicianId"
                            :options="doctorOptions"
                            optionLabel="name"
                            optionValue="id"
                            :filter="true"
                        />
                    </div>
                </div>
                <div class="p-field p-grid p-mb-2">
                    <label class="p-col-fixed p-mb-2 p-md-2 p-mb-md-0">
                        Location
                    </label>
                    <div class="p-col-fixed">
                        <p-dropdown
                            v-model="form.site"
                            :options="locationOptions"
                            optionLabel="name"
                            optionValue="id"
                            :class="{ 'p-invalid': isInvalidLocation }"
                        >
                            <template #option="slotProps">
                                <div
                                    :style="{
                                        padding: '5px',
                                        'background-color': `${slotProps.option.color}`
                                    }"
                                >
                                    {{ slotProps.option.name }}
                                </div>
                            </template>
                        </p-dropdown>
                        <small v-if="isInvalidLocation" style="color: red">
                            Location is invalid
                        </small>
                    </div>
                </div>
                <div class="p-field p-grid p-mb-2">
                    <label class="p-mb-2 p-mr-2">
                        Assign to Cycle
                    </label>
                    <div class="">
                        <p-dropdown
                            v-model="form.assignCycle"
                            :options="assignCycleOptions"
                            optionLabel="label"
                            optionValue="value"
                        />
                    </div>
                </div>
            </div>
            <div class="p-mb-2">
                <p-fieldset legend="Result" class="p-col">
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            E2
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.etwo" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            LH
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.lh" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            PROG
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.prog" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            FSH
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.fsh" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            TSH
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.tsh" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            PRL
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.prl" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            BhCG
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.bhcg" type="text" />
                        </div>
                    </div>
                    <!-- <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            Testo
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.testo" type="text" />
                        </div>
                    </div> -->
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            DHEAS
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.dheas" type="text" />
                        </div>
                    </div>
                    <div class="p-field p-grid">
                        <label class="p-col-fixed" style="width:100px">
                            AMHP
                        </label>
                        <div class="p-col">
                            <p-input-text v-model="form.amh" type="text" />
                        </div>
                    </div>
                </p-fieldset>
            </div>
        </div>
        <p-button label="Add" class="p-mr-2" @click="saveBloodTest" />
        <p-button label="Cancel" @click="cancelBloodTest" />
    </p-dialog>
</template>

<script>
import { useBloodTestAddDialogInit } from "@/composable/blood-tests";
import { ref } from "vue";

export default {
    setup() {
        const {
            addBloodDialogvisible,
            cancelBloodTest,
            saveBloodTest,
            addBlood,
            form,
            isInvalidLocation,
            locationOptions,
            formatDate,
            doctorOptions,
            cycle,
            patient,
            isCycleChecked
        } = useBloodTestAddDialogInit();

        const assignCycleOptions = ref([
            { label: "Yes", value: true },
            { label: "No", value: false }
        ]);

        return {
            addBloodDialogvisible,
            cancelBloodTest,
            saveBloodTest,
            addBlood,
            form,
            isInvalidLocation,
            locationOptions,
            formatDate,
            doctorOptions,
            cycle,
            patient,
            isCycleChecked,
            assignCycleOptions,
            cancelBloodTest: () => (addBloodDialogvisible.value = false)
        };
    }
};
</script>
